<template>
	<view class="box-compents">
		<view class="box-voice">
			<view class="xtts">{{xtts}}</view>
			<view class="butt-ts">{{buttTs}}</view>
			<view class="last-list">
				<view class="user-zi">{{text}}</view>
				<view class="iconView">
					<uni-icons custom-prefix="iconfont" type="icon-zanting" color="#fff" size="32" @tap="tochange('order')" v-show="!dhShow&&state=='order'"></uni-icons>
					<uni-icons custom-prefix="iconfont" type="icon-triangle-right" color="#fff" size="32" @tap="tochange('pause')" v-show="state=='pause'"></uni-icons>
					<div class="pl4-dh" v-show="dhShow&&state=='order'" @tap="tochange('order')">
						<div class="pl4__a"></div>
						<div class="pl4__b"></div>
						<div class="pl4__c"></div>
						<div class="pl4__d"></div>
					</div>
					 <!-- @tap="tochange('playing')" -->
					<div class="pl4" v-show="!dhShow&&state==''">
						<div class="pl4__a"></div>
						<div class="pl4__b"></div>
						<div class="pl4__c"></div>
						<div class="pl4__d"></div>
					</div>
				</view>
			</view>
			<text class="exit-zi" @tap="toexit" v-show="dhShow||state!=''">退出</text>
			
			
		</view>
		
		<u-toast ref="uToast"></u-toast>

	</view>
</template>

<script>
	export default {
		name: "goodsVoice",
		props: {
			step: {
				type: Array,
				default: () => {
					return []
				}
			},
			list: {
				type: Array,
				default: []
			},
			searchCode: {
				type: Number,
				default: 400
			},
		},
		data() {
			return {
				errTimes: uni.getStorageSync("speech_errTimes"),
				dhShow: false,
				text: "",
				state: "",
				xtts: "小麦在听，请根据提示对话",
				buttTs: "",
				secondList: [],
				nextIndex: false,
				index: 0,
				isAgain:false
			}
		},
		mounted() {
			// #ifdef APP-PLUS
			plus.speech.addEventListener("recognition", (event) => {
				// console.log("！！！！！！！！！监听 语音输入结束！！！！！！！！！！！event",event)
				
				if(this.isAgain){//通过说出第几个 再次选择
					let upNum=this.cutNum(event.result)
					let onlyNum=this.toUpperNum(upNum)
					if(onlyNum>=1){
						let arr=[]
						arr.push(this.list[onlyNum-1])
						this.list=arr
						this.chooseValue(this.nextIndex, this.index)
						this.isAgain=false
					}else{
						this.state=""
						if(uni.getStorageSync("$voice-noCon")){//有本地音频
							let audioLocal=uni.createInnerAudioContext();
							audioLocal.src = uni.getStorageSync("$voice-noCon")
							audioLocal.play();
							audioLocal.onEnded(() => {
								this.state="order"
								this.isAgain=true
								this.startRecord()
								audioLocal.destroy();
								audioLocal = null
							});
						}else{
							this.$voice({
								voiceSet: {
									tex: "没有匹配到内容",
									spd: uni.getStorageSync("voice_yusu"),
									pit: uni.getStorageSync("voice_yindiao"),
									vol: uni.getStorageSync("voice_yinliang"),
									per: uni.getStorageSync("voice_per")
								},
								audioCallback: {
									onPlay:()=>{
										this.downLoadMp3(uni._audio.src,"$voice-noCon")
									},
									onEnded: () => {
										this.state="order"
										this.isAgain=true
										this.startRecord()
									}
								}
							})
						}
					}
					return
				}
				
				if(this.step[this.index].isSkip){ //该步骤是否可以跳过，不需要输入
					let arrS=["跳过","下一步","下一项","下一个","no","不"]
					for(var i in arrS){
						if(arrS[i].indexOf(event.result)>-1){
							this.stepFunc(this.index+1)
							return
						}
					}
				}
				if (this.step[this.index].isSearchCon) { // 需要搜索内容
					if (this.step[this.index].fixedId == "lx") { // 供价类型语音输入
						this.$parent[this.step[this.index].searchFunc](event.result,false)
					}else{
						this.$parent[this.step[this.index].searchFunc](event.result)
					}
				} else { // 不需要搜索内容
					if (this.step[this.index].searchFunc) { // 需要搜索
						this.chooseValue(this.nextIndex, this.index)
					} else { // 不需要搜索
						let textRes=""
						if(this.step[this.index].fixedId=="num"||this.step[this.index].fixedId=="price"){
							var reg = /^[.0-9]+$/
							let regChina=/^[\u4e00-\u9fa5]{0,}$/
							if(reg.test(event.result)){//只存在数字和小数点
								textRes=event.result
							}else if(regChina.test(event.result)){
								let uperNum=this.cutNum(event.result)
								let xx = this.toUpperNum(uperNum);
								textRes=xx
							}else{
								textRes=event.result.replace(/[^.0-9]/ig,"")
							}
						}else if(this.step[this.index].fixedId=="switch"){//是滑动开关
							let arr=["确定","是的","是赠送商品","yes","嗯嗯嗯","对的","确认","肯定","绝对","正确","必须","中","大善"]
							textRes=false
							for(var i in arr){
								if(arr[i].indexOf(event.result)>-1){
									textRes=true
								}
							}
						}else if(this.step[this.index].fixedId=="rq"){//是日期选项
							let year=event.result.split("年")[0]
							let month=event.result.split("年")[1].split("月")[0]
							let day=event.result.split("月")[1].replace(/([^\u0000-\u00FF])/g,"")
							textRes=`${year}-${month}-${day}`
						}else if(this.step[this.index].fixedId=="save"){
							let arr=["确定","是的","保存","yes","嗯嗯嗯","对的","确认","肯定","绝对","正确","必须","中","大善"]
							for(var i in arr){
								if(arr[i].indexOf(event.result)>-1){
									this.$parent[this.step[this.index].completeFunc]()
									return
								}
							}
						}else{
							textRes=event.result
						}
						
						this.$emit("changeParentValue", {
							"fixedId": this.step[this.index].fixedId,
							"id": this.step[this.index].id,
							"val": textRes,
							"next": this.nextIndex,
							"nextFunc": this.step[this.index].nextFunc,
							"searchFunc": this.step[this.index].searchFunc,
							"isSearchCon": this.step[this.index].isSearchCon,
							"completeFunc": this.step[this.index].completeFunc
						})
					}
				}
			}, false);
			// #endif
		},
		methods: {
			toexit(){
				this.dhShow = false
				this.state=""
				this.errTimes=uni.getStorageSync("speech_errTimes")
				// #ifdef APP-PLUS
				plus.speech.stopRecognize();
				// #endif
				this.$emit("changeVoiceShow")
				return
			},
			//改变播报状态
			tochange(val){
				if(val=="order"){
					this.dhShow = false
					// #ifdef APP-PLUS
					plus.speech.stopRecognize();
					// #endif
					this.state="pause"
				}else if(val=="pause"){
					this.state="order"
					this.stepFunc(this.index)
				}
			},
			startRecord() {
				this.text = ""
				this.dhShow = true
				// #ifdef APP-PLUS
				plus.device.vibrate(300)
				// #endif
				
				var options = {};
				options.continue = false; // false:表示语音引擎根据语音输入自动结束
				options.engine = 'baidu';
				options.userInterface = false; // true:显示默认用户界面
				options.lang = "zh-cn"; // "zh-cn"-普通话；"en-us"-英语；"zh-cantonese"-粤语；   ×××"zh-henanese"-河南话（百度语音识别不支持河南话）
				// uni.getStorageSync("speech_lang");
				options.punctuation = false; // 识别结果中是否包含标点符号
				// 开始语音识别
				// #ifdef APP-PLUS
				plus.speech.startRecognize(options, (s)=> {
					this.errTimes=uni.getStorageSync("speech_errTimes")
					this.text += s;
					console.log("this.text", this.text)
					this.dhShow = false
				}, (e)=> {
					if(e.code==3&&e.message.indexOf("No Permission")>-1){
						this.$refs.uToast.show({
							type: "default",
							message: "请打开麦克风权限"
						})
						this.tochange("order")
						setTimeout(()=>{
							uni.openAppAuthorizeSetting({
								success(res) {
									
								}
							})
						},1900)
						return
					}
					// console.log("语音识别失败 eeeeeeeeeee", e)
					this.$refs.uToast.show({
						type: "error",
						message: "语音识别失败"
					})
					this.errTimes-=1
					this.dhShow = false
					if(this.errTimes>0){//没有超过3次错误机会
						this.state=""
						if(uni.getStorageSync("$voice-noHear")){//有本地音频
							let audioLocal=uni.createInnerAudioContext();
							audioLocal.src = uni.getStorageSync("$voice-noHear")
							audioLocal.play();
							audioLocal.onEnded(() => {
								this.state="order"
								this.startRecord()
								audioLocal.destroy();
								audioLocal = null
							});
						}else{
							this.$voice({
								voiceSet: {
									tex: "没有听清,请再说一次",
									spd: uni.getStorageSync("voice_yusu"),
									pit: uni.getStorageSync("voice_yindiao"),
									vol: uni.getStorageSync("voice_yinliang"),
									per: uni.getStorageSync("voice_per")
								},
								audioCallback: {
									onPlay:()=>{
										this.downLoadMp3(uni._audio.src,"$voice-noHear")
									},
									onEnded: () => {
										this.state="order"
										this.startRecord()
									}
								}
							})
						}
					}else{
						this.state="pause"
						this.dhShow=false
						// #ifdef APP-PLUS
						plus.speech.stopRecognize();
						// #endif
						if(uni.getStorageSync("$voice-pause")){//有本地音频
							let audioLocal=uni.createInnerAudioContext();
							audioLocal.src = uni.getStorageSync("$voice-pause")
							audioLocal.play();
							audioLocal.onEnded(() => {
								audioLocal.destroy();
								audioLocal = null
							});
						}else{
							this.$voice({
								voiceSet: {
									tex: "语音已暂停",
									spd: uni.getStorageSync("voice_yusu"),
									pit: uni.getStorageSync("voice_yindiao"),
									vol: uni.getStorageSync("voice_yinliang"),
									per: uni.getStorageSync("voice_per")
								},
								audioCallback: {
									onPlay:()=>{
										this.downLoadMp3(uni._audio.src,"$voice-pause")
									}
								}
							})
						}
					}
					
				});
				// #endif
			},
			endRecord() {
				this.dhShow = false
				// #ifdef APP-PLUS
				plus.speech.stopRecognize();
				// #endif
			},
			// 开始 语音播报>>语音输入 流程
			stepFunc(index) {
				this.state=""
				this.isAgain=false
				this.dhShow = false
				let nextIndex = false
				this.errTimes=uni.getStorageSync("speech_errTimes")
				if (this.step[index].isNext) { // 是否进行下一步
					if (index != this.step.length - 1) {
						nextIndex = index + 1
					}
				}
				this.nextIndex = nextIndex
				this.index = index
				this.$emit("doing", this.step[index].fixedId, index)
				let xx="$voice-"+this.step[this.index].fixedId
				if(uni.getStorageSync(xx)){//有本地音频
					let audioLocal=uni.createInnerAudioContext();
					audioLocal.src = uni.getStorageSync(xx)
					audioLocal.play();
					audioLocal.onEnded(() => {
						this.state="order"
						this.startRecord()
						if (this.step[this.index].isSearchCon) {
							
						} else { // 不需要搜索内容
							if (this.step[this.index].searchFunc) { // 需要搜索
								this.$parent[this.step[this.index].searchFunc]()
							}
						}
						audioLocal.destroy();
						audioLocal = null
					});
				}else{
					this.$voice({
						voiceSet: {
							tex: this.step[index].tips,
							spd: uni.getStorageSync("voice_yusu"),
							pit: uni.getStorageSync("voice_yindiao"),
							vol: uni.getStorageSync("voice_yinliang"),
							per: uni.getStorageSync("voice_per")
						},
						audioCallback: {
							onPlay:()=>{
								this.downLoadMp3(uni._audio.src,xx)
							},
							onEnded: () => {
								this.state="order"
								this.startRecord()
								if (this.step[this.index].isSearchCon) {
							
								} else { // 不需要搜索内容
									if (this.step[this.index].searchFunc) { // 需要搜索
										this.$parent[this.step[this.index].searchFunc]()
									}
								}
							}
						}
					})
				}
			},
			// 搜索的结果
			chooseValue(nextIndex, index) {
				let list_id = ""
				let list_mc = ""
				if (this.step[this.index].fixedId == "depart") { // 部门语音输入
					list_id = "DepartBH"
					list_mc = "DepartMC"
				} else if (this.step[this.index].fixedId == "fendian") {
					list_id = "fdbh"
					list_mc = "fdmc"
				} else if (this.step[this.index].fixedId == "business") {
					list_id = "sjbh"
					list_mc = "sjmc"
				} else if (this.step[this.index].fixedId == "storage") {
					list_id = "ckbmid"
					list_mc = "ckmc"
				} else if (this.step[this.index].fixedId == "lx") {
					list_id = "sjcxlxid"
					list_mc = "lxmc"
				}
				console.log("this.list this.list",this.list)
				if (this.list.length > 1) {
					this.state=""
					if(uni.getStorageSync("$voice-again")){//有本地音频
						let audioLocal=uni.createInnerAudioContext();
						audioLocal.src = uni.getStorageSync("$voice-again")
						audioLocal.play();
						audioLocal.onEnded(() => {
							this.state="order"
							this.isAgain=true
							this.errTimes=uni.getStorageSync("speech_errTimes")
							this.startRecord()
							audioLocal.destroy();
							audioLocal = null
						});
					}else{
						this.$voice({
							voiceSet: {
								tex: "请再次选择",
								spd: uni.getStorageSync("voice_yusu"),
								pit: uni.getStorageSync("voice_yindiao"),
								vol: uni.getStorageSync("voice_yinliang"),
								per: uni.getStorageSync("voice_per")
							},
							audioCallback: {
								onPlay:()=>{
									this.downLoadMp3(uni._audio.src,"$voice-again")
								},
								onEnded: () => {
									this.state="order"
									this.isAgain=true
									this.errTimes=uni.getStorageSync("speech_errTimes")
									this.startRecord()
								}
							}
						})
					}
				} else if (this.list.length == 1) {
					let str = ""
					if (this.step[this.index].fixedId == "shoping"){//搜索商品时特殊处理
						str=this.list[0]
					}else{
						str = `${this.list[0][list_id]}-${this.list[0][list_mc]}`
					}
					this.$emit("changeParentValue", {
						"fixedId": this.step[this.index].fixedId,
						"id": this.step[this.index].id,
						"val": str,
						"next": this.nextIndex,
						"nextFunc": this.step[this.index].nextFunc,
						"searchFunc": this.step[this.index].searchFunc,
						"isSearchCon": this.step[this.index].isSearchCon,
						"completeFunc": this.step[this.index].completeFunc
					})
				} else { //没有搜索到内容
					this.state=""
					if(uni.getStorageSync("$voice-noCon")){//有本地音频
						let audioLocal=uni.createInnerAudioContext();
						audioLocal.src = uni.getStorageSync("$voice-noCon")
						audioLocal.play();
						audioLocal.onEnded(() => {
							this.state="order"
							this.startRecord()
							audioLocal.destroy();
							audioLocal = null
						});
					}else{
						this.$voice({
							voiceSet: {
								tex: "没有匹配到内容",
								spd: uni.getStorageSync("voice_yusu"),
								pit: uni.getStorageSync("voice_yindiao"),
								vol: uni.getStorageSync("voice_yinliang"),
								per: uni.getStorageSync("voice_per")
							},
							audioCallback: {
								onPlay:()=>{
									this.downLoadMp3(uni._audio.src,"$voice-noCon")
								},
								onEnded: () => {
									this.state="order"
									this.startRecord()
								}
							}
						})
					}
				}
			},
			// 截取数字(包括小数)
			cutNum(str){
				let arrUnicode = ["\u70b9","\u96f6","\u4e00", "\u4e8c", "\u4e09", "\u56db", "\u4e94", "\u516d", "\u4e03", "\u516b",
					"\u4e5d", "\u5341", "\u767e", "\u5343","\u4e07","\u4ebf"
				]
				// let arrUnicode = ["点","零","一","二","三","四","五","六","七","八","九","十","百","千","万","亿"]
				let results=""
				str.split("").forEach((v,i,arr)=>{
					arrUnicode.forEach((item,j,arrs)=>{
						if(item==v){
							results+=v
						}
					})
				})
				return results
			},
			// 大写 数字变小写
			toUpperNum(shuzi) {
				let chnStr,xiaoshu
				if(shuzi.indexOf("点")>-1){
					chnStr=shuzi.split("点")[0]
					xiaoshu=shuzi.split("点")[1]
				}else{
					chnStr=shuzi
				}
				var chnNumChar = {
					点: ".",
					零: 0,
					一: 1,
					二: 2,
					三: 3,
					四: 4,
					五: 5,
					六: 6,
					七: 7,
					八: 8,
					九: 9
				};
				var chnNameValue = {
					十: {
						value: 10,
						secUnit: false
					},
					百: {
						value: 100,
						secUnit: false
					},
					千: {
						value: 1000,
						secUnit: false
					},
					万: {
						value: 10000,
						secUnit: true
					},
					亿: {
						value: 100000000,
						secUnit: true
					}
				};
				var expNumChar = {
					十: 10,
					十一: 11,
					十二: 12,
					十三: 13,
					十四: 14,
					十五: 15,
					十六: 16,
					十七: 17,
					十八: 18,
					十九: 19
				};
				if (expNumChar[chnStr]) {
					return expNumChar[chnStr];
				}
				var rtn = 0;
				var section = 0;
				var number = 0;
				var secUnit = false;
				var str = chnStr.split('');
				for (var i = 0; i < str.length; i++) {
					var num = chnNumChar[str[i]];
					if (typeof num !== 'undefined') {
						number = num;
						if (i === str.length - 1) {
							section += number;
						}
					} else {
						var cunit = chnNameValue[str[i]];
						if (typeof cunit == 'undefined') {
							return false;
						}
						var unit = chnNameValue[str[i]].value;
						secUnit = chnNameValue[str[i]].secUnit;
						if (secUnit) {
							section = (section + number) * unit;
							rtn += section;
							section = 0;
						} else {
							section += (number * unit);
						}
						number = 0;
					}
				}
				if(shuzi.indexOf("点")>-1){
					var xiaoStr =  xiaoshu.split('');
					var xiaoTotal=""
					for(var k in xiaoStr){
						for(var j in chnNumChar){
							if(j==xiaoStr[k]){
								xiaoTotal+=chnNumChar[j].toString()
							}
						}
					}
					return rtn + section+"."+xiaoTotal;
				}else{
					return rtn + section;
				}
			},
			downLoadMp3(url,setname){
				uni.setStorageSync(setname,"")
				uni.downloadFile({
					url:url,
					success(res) {
						uni.saveFile({
							tempFilePath:res.tempFilePath,
							success:function(resave){
								let savedFilePath=resave.savedFilePath
								uni.setStorageSync(setname,savedFilePath)
								// console.log("uni.getStorageSync setname:"+setname,"！！！path:"+savedFilePath)
							},
							fail:function(errs){}
						})
					},
					fail(err) {}
				})
			}
			
		},
		watch: {
			step: function(newv, oldv) {
				if (newv.length != 0) {
					this.stepFunc(0)
				}
			},
			searchCode: function(newv, oldv) {
				if (this.step.length != 0) {
					if (newv == 0) { //已经查询到搜索内容
						this.chooseValue(this.nextIndex, this.index)
					}
				}
			},
			state: function(newv, oldv) {
				if (newv == "order") {
					this.xtts="小麦在听，请根据提示对话"
					this.buttTs="点击下方按键，停止语音对话"
				}else if (newv == "pause") {
					this.xtts="已暂停，再继续语音对话吧"
					this.buttTs="点击下方按键，开始语音对话"
				}else{
					this.xtts="小麦在听，请根据提示对话"
					this.buttTs=""
				}
			},
		}

	}
</script>

